/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/* CSS reset end */


/* font */
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap');


/* normal */
*,*::before,*::after{
    box-sizing: border-box;
}
body{
    text-align: center;
    font-family: 'Roboto', sans-serif;
    font-style: italic;
}
.container{
    width: 1200px;
    margin: 0 auto;
}


header{
    background: url('https://hexschool.github.io/webLayoutTraining1st/week2/banner.png') no-repeat center;
    background-size: cover;
    height: 958px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #707070;
}
.main{
    color: white;
    text-align: left;
    font-weight: 700;
}
.main h1{
    margin-bottom: 32px;
}
.small-title{
    display: block;
    font-size: 24px;
    line-height: 42px;
}
.title{
    display: block;
    font-size: 80px;
    line-height: 106px;
    font-weight: 900;
    margin-bottom: 32px;
}
.main p{
    font-size: 24px;
    line-height: 36px;
    padding: 0 13px;
    margin-bottom: 40px;
}
.icon{
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
}
.icon a:hover{
    opacity: 0.8;
}


.about{
    padding: 56px 0;
    font-size: 24px;
    font-weight: 700;
}
.about h2{
    font-size: 64px;
    font-weight: 500;
    line-height: 84px;
    margin-bottom: 40px;
}
.about img{
    display: block;
    width: 384px;
    margin: 0 auto 20px auto;
}
.about h3{
    line-height: 42px;
    margin-bottom: 4px;
}
.about .mb-8{
    margin-bottom: 8px;
}
.about p{
    line-height: 36px;
    margin-bottom: 24px;
}
.about ul{
    width: 384px;
    margin: 0 auto;
    border-top: 1px solid #000;
    padding-top: 28px;
}
.about li{
    line-height: 42px;
    margin-bottom: 28px;
}
.about li::before{
    content: '';
    display: inline-block;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: #891818;
    margin-right: 16px;
    vertical-align: middle;
}
