.brand{
    color: white;
    background-color: #000;
    padding: 56px 0 160px 0;
}
.brand h2{
    font-size: 64px;
    line-height: 84px;
    font-weight: 500;
    margin-bottom: 12px;
}
.brand h3{
    font-size: 24px;
    line-height: 42px;
    margin-bottom: 32px;
}
.brand p{
    font-size: 20px;
    line-height: 30px;
    font-style: normal;
    margin-bottom: 68px;
}
.brand-pic{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.brand-pic img{
    display: block;
}
.mb-40{
    margin-bottom: 40px;
}
.mr-24{
    margin-right: 24px;
}


.work{
    padding: 56px 0 96px 0;
    font-weight: 500;
}
.work h2{
    font-size: 64px;
    line-height: 84px;
    margin-bottom: 16px;
}
.work p{
    font-size: 20px;
    line-height: 30px;
    font-style: normal;
    margin-bottom: 56px;
}
.work-list{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.work-list li{
    margin-bottom: 48px;
}
.work-list img{
    display: block;
    margin-bottom: 16px;
}
.work-list p{
    line-height: 42px;
    margin-bottom: 0;
}


footer{
    background-color: #000;
    color: #FFF;
    padding: 64px 0 48px 0;
}
footer h2{
    font-size: 64px;
    line-height: 84px;
    font-weight: 500;
    margin-bottom: 16px;
}
footer li{
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 16px;
}
footer li a{
    color: #FFF;
    text-decoration: none;
}
footer li a:hover{
    color: rgba(255, 255, 255, 0.7);
}